.circle{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 400;
    background-color: #FF4740;
    background-color: var(--orange);
    -webkit-clip-path: circle(50px at 0% 0%);
            clip-path: circle(50px at 0% 0%);
    height: 50px;
    width: 50px;
    display: none;
    transition: display 0.5s ease-in;
}
.end-circle{
        position: fixed;
        bottom: 0;
        right: 0;
        z-index: 40;
        background-color: #FF4740;
        background-color: var(--orange);
        -webkit-clip-path: circle(50px at 100% 100%);
                clip-path: circle(50px at 100% 100%);
        height: 50px;
        width: 50px;  
        display: none;
    transition: display 0.5s ease-in; 
}
.end-circle i {
    padding: 15px;
    font-size: 1.5rem;
    cursor: pointer;
}
.circle i{
    padding: 5px;
    font-size: 1.5rem;
    cursor: pointer;
}



.section.rotate + nav ul li{
    -webkit-transform: translateX(0);
            transform: translateX(0);
    transition-delay: 0.2s;
}
nav{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 0;
}
nav ul{
    list-style-type: none;
    padding: 10px;
    color: ghostwhite;
    color: var(--white);
    
}

nav ul li{
    padding: 7px;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 1s linear;
    transition: transform 1s linear;
    transition: transform 1s linear, -webkit-transform 1s linear;  
    cursor: pointer;  
}
/* nav ul li.links:hover{
    color: var(--green);
    font-size: 1.1rem;
} */
.links{
    text-decoration: none;
    color: ghostwhite;
    color: var(--white);
}
.links :hover{
    color: #3affb9;
    color: var(--green);   
}
#home:hover{
    color: #3affb9;
}
nav ul li +li{
    margin-left: 5px;
    -webkit-transform: translateX(-150%);
            transform: translateX(-150%);
     
}
nav ul li + li + li{
    margin-left: 10px;
    -webkit-transform: translateX(-250%);
            transform: translateX(-250%);
     
}
nav ul li + li + li +li{
    margin-left: 15px;
    -webkit-transform: translateX(-350%);
            transform: translateX(-350%);
     
}


.navigate{
    position: fixed;
        top: 0;
        right: 0;
        z-index: 0;
}
.navigate ul{
    list-style-type: none;
    padding: 10px;
    color: ghostwhite;
    color: var(--white);
    font-size: 1.7rem;
    margin-right: 10px;
}
.navigate ul li{  
    -webkit-transform: translateX(300%);  
            transform: translateX(300%);
    transition: -webkit-transform 1s linear;
    transition: transform 1s linear;
    transition: transform 1s linear, -webkit-transform 1s linear;  
    cursor: pointer;  
    padding: 10px;
}
.navigate ul li i:hover{
    color: #3affb9;
    color: var(--green);   
}
ul a{
    color: #FF4740;
    color: var(--orange);

}

:root {
